import * as React from "react";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";

export default function Top() {
  return (
    <Container
      disableGutters
      maxWidth="sm"
      component="main"
      sx={{ pt: 4, pb: 4 }}
    >
      <Typography
        component="h1"
        variant="h3"
        align="center"
        color="text.primary"
        gutterBottom
      >
        タイプ相性表（擬き）
      </Typography>
      <Typography
        variant="h6"
        align="center"
        color="text.secondary"
        component="p"
      >
        20時間くらいかけたのに、絞り込み・props間の値の受け渡し・toggleButtonによる要素の表示OnOff・listの横書き表示・descriptionのループの何一つ習得できなかったってマジ？
        相手のタイプもわからないし読みにく過ぎるでしょ…
      </Typography>
    </Container>
  );
}
